@font-face {
  font-family: "yandexBlack";
  src: url("../fonts/text-black.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "yandexMedium";
  src: url("../fonts/text-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "yandexRegular";
  src: url("../fonts/text-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
*,
::after,
::before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  height: 100%;
}

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: inline-block;
  vertical-align: top;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  background-color: var(--default-white);
  font-family: var(--main-font);
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-style: normal;
  min-height: 100%;
  color: var(--black1);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 0;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0);
}

:root {
  --default-black: #000000;
  --default-white: #ffffff;
  --arial: "Arial", "sans-serif", "Verdana";
  --tablet-width: 768px;
  --desktop-width: 1440px;
  --mobile-width: 375px;
  --main-font: "yandexRegular", "Arial", "sans-serif";
  --yandex-black-font: "yandexBlack", "Arial", "sans-serif";
  --yandex-medium-font: "yandexMedium", "Arial", "sans-serif";
  --mobile-padding: 32px;
  --tablet-padding: 70px;
  --desktop-r-padding: 98px;
  --desktop-l-padding: 262px;
  --black1: #21201F;
  --white1: #f1f1f1;
  --grey1: #808080;
  --grey2: #c5c5c5;
  --yellow1: #FFDE40;
}

